import React from 'react';
import { Slide, Paper, makeStyles, Button, Container } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
    page: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    content: {
        position: "relative",
        top: "45px",
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    prevPage: {
        position: "absolute",
        top: 0,
        left: "50%",
        backgroundColor: "lightblue",
    },
    nextPage: {
        position: "absolute",
        bottom: 0,
        left: "50%",
        backgroundColor: "lightblue",
    },
}));

function PortfolioPage(props) {
    const classes = useStyles();

    return (
        <Container>
            {props.display && props.prevPage ? <Button className={classes.prevPage} onClick={props.prevPage}>Prev</Button> : undefined}
            <Slide {...props.slide}>
                <Paper className={classes.content}>
                    {props.children}
                </Paper>
            </Slide>
            {props.display && props.nextPage ? <Button className={classes.nextPage} onClick={props.nextPage}>Next</Button> : undefined}
        </Container>
    )
}
export default PortfolioPage