import React, { useState } from 'react';
import AppContext from './context/AppContext';
import Preface from './pages/Preface';
import Welcome from './pages/Welcome';
import Experiences from './pages/Experiences';

function App(props) {
    const [currentPage, setCurrentPage] = useState("welcome");

    const _props = {
        welcome: {
            page: {
                display: currentPage === "welcome",
                slide: {
                    direction: currentPage === "welcome" ? "down" : "down",
                    in: currentPage === "welcome",
                    timeout: { enter: 1000, exit: 0 },
                    mountOnEnter: true,
                    unmountOnExit: true
                },
                nextPage: () => {
                    setCurrentPage(() => "preface");
                }
            }
        },
        preface: {
            page: {
                display: currentPage === "preface",
                slide: {
                    direction: currentPage === "preface" ? "up" : "down",
                    in: currentPage === "preface",
                    timeout: { enter: 1000, exit: 0 },
                    mountOnEnter: true,
                    unmountOnExit: true
                },
                prevPage: () => {
                    setCurrentPage(() => "welcome");
                },
                nextPage: () => {
                    setCurrentPage(() => "experiences");
                }
            }
        },
        experiences: {
            page: {
                display: currentPage === "experiences",
                slide: {
                    direction: currentPage === "experiences" ? "up" : "down",
                    in: currentPage === "experiences",
                    timeout: { enter: 1000, exit: 0 },
                    mountOnEnter: true,
                    unmountOnExit: true
                },
                prevPage: () => {
                    setCurrentPage(() => "preface");
                },
                nextPage: () => {
                    setCurrentPage(() => "welcome");
                }
            }
        }
    }

    return (
        <AppContext.Provider>
            <Experiences {..._props.experiences} />
            <Preface {..._props.preface} />
            <Welcome {..._props.welcome} />
        </AppContext.Provider>
    )
}
export default App;